body {
  margin: 0;
  padding: 0;
}
body #app {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
}
body * {
  font-family: cursive;
}

@media screen and (max-width: 1000px) {
  * {
    font-size: 0.95em;
  }
}
header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  text-transform: uppercase;
  grid-area: hd;
  padding: auto;
  cursor: pointer;
  -webkit-animation: top 3s forwards -1s;
          animation: top 3s forwards -1s;
}
header > a {
  color: #2CAEF9;
}



