body {
  margin: 0;
  padding: 0;
}
body #app {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
}
body * {
  font-family: cursive;
}

@media screen and (max-width: 1000px) {
  * {
    font-size: 0.95em;
  }
}
nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  text-transform: uppercase;
  grid-area: nv;
  -webkit-animation: top 3s forwards -1s;
          animation: top 3s forwards -1s;
}
nav a {
  text-decoration: none;
  padding: 0 0 0 1em;
  cursor: pointer;
  color: inherit;
}
nav a:hover {
  color: #2CAEF9;
}
@-webkit-keyframes top {
  from {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes top {
  from {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}



