body {
  margin: 0;
  padding: 0;
}
body #app {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
}
body * {
  font-family: cursive;
}

@media screen and (max-width: 1000px) {
  * {
    font-size: 0.95em;
  }
}
button.buttonified {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #fff;
  padding: 0.5em;
  background-color: #2CAEF9;
  -webkit-box-shadow: 0.05em 0.1em 0.6em 0.1em rgba(0, 0, 0, 0.4);
          box-shadow: 0.05em 0.1em 0.6em 0.1em rgba(0, 0, 0, 0.4);
  border: 0.2em solid transparent;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  outline: 0;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  font-weight: bolder;
  text-transform: capitalize;
  margin: 0.2em;
  padding: 0.5em;
}
button.buttonified.rounded {
  border-radius: 100em;
}
button.buttonified.extended {
  border-radius: 100em;
  padding: 0.2em 1em;
}
button.buttonified.curved {
  border-radius: 0.5em;
}
button.buttonified.outlined {
  background: transparent;
  border: 0.15em solid;
  -webkit-box-shadow: none;
          box-shadow: none;
  color: #21252B;
}
button.buttonified.flat {
  background: transparent;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  color: #21252B;
}
button.buttonified:hover {
  -webkit-transform: scale(0.95);
      -ms-transform: scale(0.95);
          transform: scale(0.95);
  -webkit-filter: contrast(70%);
          filter: contrast(70%);
}
button.buttonified.flat:hover {
  background: rgba(0, 0, 0, 0.2);
}

a {
  text-decoration: none;
}



