body {
  margin: 0;
  padding: 0;
}
body #app {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
}
body * {
  font-family: cursive;
}

@media screen and (max-width: 1000px) {
  * {
    font-size: 0.95em;
  }
}
.loader {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  background-color: #21252B;
  color: #FFF;
}

svg {
  width: 15em;
}

.cls-2, .cls-3 {
  fill: none;
  stroke-dasharray: 800;
  stroke-dashoffset: 0;
  stroke-width: 10px;
}

.cls-2 {
  stroke: #2caef9;
  -webkit-animation: stroker 5s linear infinite;
          animation: stroker 5s linear infinite;
}

.cls-3 {
  stroke: #fff;
  -webkit-animation: stroker 5s 2.5s linear reverse infinite;
          animation: stroker 5s 2.5s linear reverse infinite;
}

@-webkit-keyframes stroker {
  0% {
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dashoffset: 800;
  }
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes stroker {
  0% {
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dashoffset: 800;
  }
  100% {
    stroke-dashoffset: 0;
  }
}



